.footer {
  background: #f7f7f7;
  margin: 0;
}

.footer-body-content {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.footer-font-flex {
  flex-direction: column;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  gap: max(.75rem, min(.5313rem + 1.0938vw, 1.4063rem));
  display: flex;
}

.footer-font-size {
  color: #545454;
  text-align: center;
  font-family: Roboto;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.footer-color-font {
  color: #b92f2c;
  text-decoration: none;
}

.footer-heart-icon-custom {
  width: 14px;
  height: 14px;
  fill: #b92f2c;
}

.footer-is-hidden {
  visibility: hidden;
  transform: scale(1.1);
}

.footer-modal {
  z-index: 10;
  width: 100vw;
  height: 100vh;
  pointer-events: visiblePainted;
  background: rgba(0, 0, 0, .2);
  justify-content: center;
  align-items: center;
  transition: all .4s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.footer-modal.is-visible {
  visibility: visible;
}

.footer-modal-content {
  width: 400px;
  height: auto;
  background-color: #fff;
  border-radius: 20px;
  align-content: center;
  margin: 0 auto;
  padding: 25px 10px;
  position: relative;
}

.footer-modal:hover {
  opacity: .95;
  background: none;
}

.footer-modal-close {
  fill: #b92f2c;
  cursor: pointer;
  width: max(1.08rem, min(.7075rem + 1.8625vw, 2.1975rem));
  height: max(1.08rem, min(.7075rem + 1.8625vw, 2.1975rem));
  background-color: #fff;
  border-radius: 12px;
  outline: none;
  justify-content: center;
  align-items: center;
  padding: 2px 4px;
  font-size: 24px;
  display: flex;
  position: absolute;
  top: 10px;
  right: 10px;
}

.footer-modal-close:hover {
  background: none;
  border: 2px solid #b92f2c;
}

.footer-close-icon {
  width: max(.4338rem, min(.3383rem + .4771vw, .72rem));
  height: max(.4338rem, min(.3383rem + .4771vw, .72rem));
}

.footer-close-icon:hover {
  fill: #b92f2c;
}

.footer-font-title {
  color: #000;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  margin-top: 2px;
  font-family: Roboto;
  font-size: max(.5206rem, min(.3942rem + .6323vw, .9rem));
  font-style: normal;
  font-weight: 500;
  line-height: 14px;
  display: flex;
}

.footer-content {
  height: auto;
  margin: 0 auto;
  list-style-type: none;
}

.footer-cards {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.footer-team-card {
  width: 95%;
  height: auto;
  border: 2px solid #b92f2c;
  border-radius: 20px;
  flex-direction: row;
  gap: 10px;
  padding: 5px;
  display: flex;
  box-shadow: 5px 5px 14px -3px rgba(185, 47, 44, .67);
}

.card-team {
  width: 80%;
  height: 85%;
  border: 2px solid;
  border-radius: 5px;
  margin-bottom: 10px;
}

.footer-icon:hover {
  cursor: pointer;
  background: #b92f2c;
}

.footer-image-container {
  width: 125px;
  height: 110px;
  object-fit: cover;
  border: 2px solid #b92f2c;
  border-radius: 20px;
  margin: 0 auto;
  display: block;
  overflow: hidden;
}

.footer-member-info {
  box-sizing: border-box;
  flex-direction: column;
  flex-grow: 1;
  justify-content: center;
  align-items: start;
  gap: 15px;
  padding: 10px;
  display: flex;
}

.footer-team-name {
  color: #000;
  text-align: center;
  letter-spacing: .48px;
  font-family: Roboto;
  font-size: max(.625rem, min(.4583rem + .8333vw, 1.125rem));
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.footer-team-function {
  color: #757575;
  text-align: center;
  letter-spacing: .48px;
  font-family: Roboto;
  font-size: max(.4338rem, min(.3383rem + .4771vw, .72rem));
  font-style: normal;
  font-weight: 500;
  line-height: 133.333%;
}

.footer-icon-cards {
  border: 2px solid #b92f2c;
  border-radius: 100px;
  flex-direction: column;
  flex-shrink: 0;
  align-content: center;
  justify-content: center;
  align-self: center;
  align-items: center;
  gap: 6px;
  padding: 14px;
  display: flex;
}

.footer-linkedin-icon {
  width: 20px;
  height: 20px;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
  background-image: url("linkedin.e61f3535.svg");
  display: inline-block;
}

.footer-github-icon {
  width: 20px;
  height: 20px;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
  background-image: url("github.23df02cc.svg");
  display: inline-block;
}

.footer-linkedin-icon:hover, .footer-github-icon:hover {
  background-color: #b92f2c;
}

.footer-dark-mode {
  color: #fff;
  background-color: #2f303a;
}

